.slider_panel{
    width: 7680px;
    position: relative;
  }
  .slider_image{
    width: 1280px;
    height: 860px;
    float: left;
  }
  .slider_text_panel{
    position:absolute;
    top: 300px;
  }
  .slider_text{
    position: absolute;
    width: 300px;
    height: 200px;
    color: darkgray;
    top: 50px;
    left: 200px;
}
.slider_text h2{
    font-size: 50px;
}
.slider_text p {
    font-size: 30px;
}
  .leftright{
    width: 1280px;
    height: 860px;
    position: absolute;
  }
  .left{
    position: absolute;
    top: 400px;
  }
  .right{
    position: absolute;
    top: 400px;
    left: 1180px;
  }
  .serviceIcon{
    position: absolute;
    left: 1150px;
    top: 80px;
    background-color: white;
  }
  .serviceIcon img {
    width: 100px;
    height: 70px;
    position: relative;
    padding: 10px;
  }
  .serviceIcon img:hover{
    background-color: blanchedalmond;

  }