.Top3{
    position: relative;
}
.Cust{
    width: 1280px;
    height: 500px;
    background: linear-gradient(
        to left,
        rgba(255,255,255,0) 10%,
        rgba(255,255,255,1) 50%,
        rgba(255,255,255,1) 100%
    ),url('../public/img/고객센터.jpg');
    background-repeat: no-repeat;
    background-size: contain;
}
.Cust img{
    vertical-align: middle;
    width: 40px;
    height: 40px;
    margin-right: 10px;
}
.Cust h2{
    padding: 20px;
    color: gray;
}
.Cust h3{
    color: gray;
}
.Cust .Ques{
    margin-top: 30px;
    line-height: 40px;
    font-size: 17px;
    font-weight: bold;
}
.Ques a{
    text-decoration: none;
    color: gray;
}
.Ques a:hover{
    color: black;
}
.Menu{
    text-align: center;
    margin-top: 30px;
}
.Menu ul{
   display: inline-block;
   position: relative;
   height: 52px;
}
.Menu li{
    display: inline-block;
}
.tapon{
    text-decoration: none;
    color: black;
    font-weight: bold;
    border: 1px solid black;
    padding:15px 150px;
    background-color: #f7f8f8;
}
.active{
    color: white;
    background-color: black;
    text-decoration: underline;
}
.serve{
    margin-top: 100px;
}
.array{
margin-left: 90px;
}
.Servemom li{
    display: inline-block;
    list-style: none;
    border: 1px solid gray;
    padding: 50px;
    text-align: center;
}
.Servemom li img{
    margin-left: 20px;
    width: 150px;
    height: 150px;
}
.Servemon li .write{
    text-decoration: none;
}
.par{
    position: relative;
}
.par:hover{
    cursor: pointer;
    background-color: bisque;
}
.write{
margin-top: 10px;
position: absolute;
left: 90px;
font-size: 20px;
font-weight: bold;
}
.numb{
    border: 1px solid gray;
    margin-right: 10px;
    border-radius: 3px;
    padding: 2px;
    background-color:azure;
}

