html, body, div, header, section,footer,ul, ol, li, h1, h2, h3, p{
  margin:0;
  padding:0;
}
ul{
  list-style: none;
}
body{
  width: 1280px;
  margin: 0 auto;
}
.nav{
  position: relative;
  overflow: hidden; 
}
.header{
  width: 1280px;
  height: 80px;
}
.header ul{
  position: absolute;
  text-decoration: none;
}
.header ul li {
  padding: 10px;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin-right: 100px;
  }
  .font{
    color: black;
    text-decoration: none;
    font-weight: bold;
    font-size: 20px;
  }
.header ul li img{
    width: 300px;
    height: 50px;
  }
.slider_panel{
  width: 7680px;
  position: relative;
}
.slider_image{
  width: 1280px;
  height: 860px;
  float: left;
}
